html {
  --text-color: #000;
  --color-active-link: #00acdc;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-of-type) {
  margin-left: 20px;
}

.nav__link {
  color: var(--text-color);
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active, .nav__link:hover {
  color: var(--color-active-link);
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

